* {
    box-sizing: border-box;
}

body {
    margin: 0;
}

.nav{
    background-color: #000;
    color: whitesmoke;
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    padding: 0 1rem;
}


.nav ul {
    padding: 10px;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 1rem;
}

.nav a {
    color:inherit;
    text-decoration: none;
    display: flex;
    gap: .25rem;
}

.nav li.active{
    color: aqua;
}

.nav li:hover{
    color: aqua;
}